@keyframes bounce { from, 20%, 53%, 80%, to { animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: translate3d(0, 0, 0); }
  40%, 43% { animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -30px, 0); }
  70% { animation-timing-function: cubic-bezier(0.755, 0.05, 0.855, 0.06);
    transform: translate3d(0, -15px, 0); }
  90% { transform: translate3d(0, -4px, 0); } }

@keyframes pop { from, 20%, 40%, 60%, 80%, to { animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% { opacity: 0;
    transform: scale3d(1, 1, 1); }
  20% { transform: scale3d(1.1, 1.1, 1.1); }
  40% { transform: scale3d(0.9, 0.9, 0.9); }
  60% { opacity: 1;
    transform: scale3d(1.03, 1.03, 1.03); }
  80% { transform: scale3d(0.97, 0.97, 0.97); }
  to { opacity: 1;
    transform: scale3d(1, 1, 1); } }

@keyframes shake { from, to { transform: translate3d(0, 0, 0); }
  10%, 30%, 50%, 70%, 90% { transform: translate3d(-10px, 0, 0); }
  20%, 40%, 60%, 80% { transform: translate3d(10px, 0, 0); } }

@keyframes pulse { from { transform: scale3d(1, 1, 1); }
  50% { transform: scale3d(1.05, 1.05, 1.05); }
  to { transform: scale3d(1, 1, 1); } }

@keyframes scaleUp { 0% { transform: scale(1); }
  to { transform: scale(1.1); } }

@keyframes scaleUpPop { from, 20%, 40%, 60%, 80%, to { animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% { transform: translateZ(30px) scale(1); }
  25% { transform: translateZ(30px) scale(1.15); }
  50% { transform: translateZ(30px) scale(0.98); }
  75% { transform: translateZ(30px) scale(1.13); }
  to { transform: translateZ(30px) scale(1.08); } }

@keyframes scaleDown { 0% { transform: translateZ(30px) scale(1.08); }
  to { transform: translateZ(0) scale(1); } }

